.notification {
    position: fixed;
    bottom: 30px;
    left: 30px;
    background-color: #343434;
    padding: 15px;
    border-radius: 4px;
    min-width: 320px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    font-family: 'Montserrat';

    &__close {
        cursor: pointer;
        border: 0;
        box-shadow: none;
        font-size: 0;
        width: 24px;
        height: 24px;
        background-color: transparent;
    }
}